
const {ccclass, property} = cc._decorator;
 
@ccclass
export default class NewClass extends cc.Component {
 
 
    // LIFE-CYCLE CALLBACKS:
    speed:number = 0;
    move:number = 0;
 
    onLoad () {
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN,this.MoveSpike,this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP,this.StopSpike,this);
    }

    MoveSpike(event){
        switch(event.keyCode){
            case cc.macro.KEY.right:
                this.speed = 1000;
                break;
            case cc.macro.KEY.left:
                this.speed = 1000;
                break;
        }
    }

    StopSpike(event){
        switch(event.keyCode){
            case cc.macro.KEY.right:
                this.speed = 0;
                break;
            case cc.macro.KEY.left:
                this.speed = 0;
                break;
        }
    }
 
    start () {
        this.node.getComponent(cc.Animation).play();
    }
 
    update (dt) {

        if(this.move == 0){
            this.node.rotation -= this.speed*dt;
            this.node.setPosition(this.node.position.x ,this.node.position.y -= this.speed*dt);
        }

        if(this.move == 1){
            this.node.rotation += this.speed*dt;
            this.node.setPosition(this.node.position.x ,this.node.position.y += this.speed*dt);
        }

        if(this.node.position.y <= -240){
            this.move = 1
        }

        if(this.node.position.y >= 270){
            this.move = 0
        }
    }
}