
const {ccclass, property} = cc._decorator;
 
@ccclass
export default class NewClass extends cc.Component {
 
    moveRight:number = 0;
    moveLeft:number = 0;
    speed:number = 200;
    actualSpeed:number = 0;
    animation:number = 0;
    saltando:boolean = false;
    bajando:boolean = false;
    posicionY:number = 0;

    @property(cc.Scene)
    current_scene:cc.Scene = null;
 
 
    // LIFE-CYCLE CALLBACKS:
    onCollisionEnter(otherCollider,selfCollider){
        if(otherCollider.name == "SpikeBall<CircleCollider>" && selfCollider.name == "Man<BoxCollider>"){
            cc.director.loadScene(this.current_scene.name);
        }
        if(otherCollider.name == "SpikeBall2<CircleCollider>" && selfCollider.name == "Man<BoxCollider>"){
            cc.director.loadScene(this.current_scene.name);
        }
     }
   
 
    MoveMan(event){
        switch(event.keyCode){
            case cc.macro.KEY.right:
                this.moveRight = 1;
                this.moveLeft = 0;
                this.actualSpeed = 1;
                break;
            case cc.macro.KEY.left:
                this.moveLeft = 1;
                this.moveRight = 0;
                this.actualSpeed = 1;
                break;
            case cc.macro.KEY.space:
                if(this.saltando == false && this.bajando ==false){
                    this.saltando = true;
                    this.node.getComponent(cc.Animation).stop('ManRunAnimation');
                    this.node.getComponent(cc.Animation).play('ManJumpAnimation');
                }
                break;    
        }
    }

    StopMan(event){
        switch(event.keyCode){
            case cc.macro.KEY.right:
                this.moveRight = 0;
                this.node.getComponent(cc.Animation).stop();
                this.actualSpeed = 0;
                this.animation = 0;
                break;
            case cc.macro.KEY.left:
                this.moveLeft = 0;
                this.node.getComponent(cc.Animation).stop();
                this.actualSpeed = 0;
                this.animation = 0;
                break;
        }
    }
 
    onLoad () {
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN,this.MoveMan,this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP,this.StopMan,this);

        this.current_scene = cc.director.getScene();
    }
 
    start () {
    }
 
    update (dt) {

        if(this.node.x <= -455){
            this.node.setPosition(-454 ,this.node.position.y);
        }

        if(this.moveRight == 1){
            this.node.scaleX = 1;
            this.node.setPosition(this.node.position.x+= this.speed*dt ,this.node.position.y);
        }
        if(this.moveLeft == 1){
            this.node.scaleX = -1;
            this.node.setPosition(this.node.position.x-= this.speed*dt ,this.node.position.y);
        }
        if(this.saltando == false && this.bajando == false){
            this.posicionY = this.node.y;
        }
        if(this.saltando == true){
            this.node.setPosition(this.node.position.x ,this.node.position.y += (this.speed+100)*dt);
        }
        if(this.posicionY <= this.node.y - 100){
            this.bajando = true;
            this.saltando = false;
        }
        if(this.bajando == true){
            this.node.setPosition(this.node.position.x ,this.node.position.y -= (this.speed+100)*dt);
            if(this.posicionY >= this.node.y){
                this.node.setPosition(this.node.position.x ,this.posicionY);
                this.bajando = false;
                this.animation = 0;
            }
        }

        if(this.actualSpeed == 1 && this.animation == 0){
            this.node.getComponent(cc.Animation).play('ManRunAnimation');
            this.animation = 1;
        }
    }
}