
const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    score:cc.Label = null;

    time:number = 10;
    moveTime:number = 0;

    // LIFE-CYCLE CALLBACKS:

    LoseTime(){
        if(this.moveTime == 1){
            this.time -= 1;
            this.score.string = "" + this.time.toString();
        }
    }

    MoveTime(event){
        switch(event.keyCode){
            case cc.macro.KEY.right:
                this.moveTime = 1;
                break;
            case cc.macro.KEY.left:
                this.moveTime = 1;
                break;
            case cc.macro.KEY.r:
                cc.director.loadScene(cc.director.getScene().name);
                break;
        }
    }

    StopTime(event){
        switch(event.keyCode){
            case cc.macro.KEY.right:
                this.moveTime = 0;
                break;
            case cc.macro.KEY.left:
                this.moveTime = 0;
                break;
        }
    }

    onLoad () {
        var manager = cc.director.getCollisionManager();
        manager.enabled = true;

        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN,this.MoveTime,this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP,this.StopTime,this);

        this.schedule(this.LoseTime,1,cc.macro.REPEAT_FOREVER,0);
    }

    start () {

    }

    update (dt) {
        if(this.time == 0){
            cc.director.loadScene(cc.director.getScene().name);
        }
    }
}